import React from "react";
import "../css/result.css";
import {ResultDB} from "../component/ResultDB";
import Samsung from "../component/Samsung.png";
class result extends React.Component{
    state = {
        value : 0,
        array : {} ,
        selectmenu : 0,
        
    }
    selectFirst = () =>{
        this.setState(current => ({selectmenu : 0}));
    }
    selectSecond = () =>{
        this.setState(current => ({selectmenu : 1}));
    }
    selectThird = () =>{
        this.setState(current => ({selectmenu : 2}));
    }
    render(){
        const{location} = this.props;
        for(let i = 0;i< ResultDB.length;i++){
            if(location.state.mbti === ResultDB[i].id){
                this.state.value = i;
                break;
            } 
        }
        return (
            <div className = "result_explain">
                <div className ="result_subtitle">
                    국내 주식의 이단아<div> </div>
                </div>
                <div className = "result_title">
                    삼성전자
                </div>
                <div className = "result_img">
                    <img src = {Samsung} height = '200' weight = '200'/>
                </div>
                <div className = "result_personal">
                    <ul className = "result_personal_font">
                        <li className = "result_personal_line">
                            뭔가 위험한데 재밌어 보이는 것은 무조건 해요.<br/>
                            안 다치고 후다닥 성공하면 세상 기뻐해요.
                        </li>
                        <li className = "result_personal_line">
                            마트에서 사야 할 것만 후루룩 사고 가요.<br/>
                            그런데 과자 코너에서는 세상 심각하게 고민해요.
                        </li>
                        <li className = "result_personal_line">
                            차 타고 가다가 점프대처럼 생긴 것이 보이면 "가능할까" 생각해요.
                        </li>
                        <li className = "result_personal_line">
                            핵인싸가 되고 싶어 하는데, 여가 시간에 혼자 있는 걸 좋아하는 편이에요.
                        </li>
                        <li className = "result_personal_line">
                            가끔 심각한 표정을 짓는데, 별일 아닌 경우가 많아요." 파리는 어떻게 천장에 붙어있지" 이런 고민 하고있어요.
                        </li>
                    </ul>
                </div>
                <div className = "result_best">
                    <div className = "result_best_title">
                        <div className = "result_font">같이 떡상할 종목</div>
                    </div>
                </div>
                <div className = "result_worst">
                    <div className = "result_worst_title">
                    <div className = "result_font">같이 떡락할 종목</div>
                    </div>
                </div>
            </div>
        )
        } 
    }



export default result;